.card {
    background-color: white;
    position: relative;
    width: 300px;
    height: 60vh;
    padding: 20px;
    max-width: 85vh;
    box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
    background-size: cover;
    background-position: center;
    border-radius: 20px;
}

.tinderCard__cardContainer {
    display: flex;
    justify-content: center;
    margin-top: 2vh;
}

.swipe {
    position: absolute;
}

.cardContent {
    width: 100%;
    height: 100%;
}

.card__h3 {
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: #fff;
}